/* -- Header -- */
.header {
  position: relative;
  display: block;
  /* -- Navigation -- */
}

.header-top-spot {
  height: 85px;
}

.navbar {
  position: relative;
  max-height: 1000px;
  height: 60px;
  z-index: 1200;
  width: 100%;
}

.navbar-fixed {
  position: fixed;
  left: 0;
  top: 0;
}

.navbar-layout {
  transition: box-shadow .15s ease-in, background .15s ease-in;
  box-shadow: 0 0px 15px rgba(0, 0, 0, 0);
  border-radius: 0 0 18px 18px;
  background: transparent;
  margin: 0 auto;
}

.navbar-focus .navbar-layout, .navbar-show .navbar-layout, .navbar:hover .navbar-layout {
  box-shadow: 0 5px 25px rgba(0, 0, 50, 0.12);
  background: #fff;
}

.navbar-head {
  transition: border-bottom-color .45s linear;
  border-bottom: 1px solid transparent;
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  display: flex;
}

.navbar-title {
  font-family: "Bungee Inline", sans-serif;
  letter-spacing: 0.05em;
  text-decoration: none;
  line-height: 60px;
  padding-left: 25px;
  position: relative;
  font-size: 17px;
}

.navbar-list {
  transition: max-height .55s ease-in-out;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  overflow-y: hidden;
  list-style: none;
  display: flex;
  width: 100%;
  margin: 0;
}

.navbar-link {
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  line-height: 60px;
  letter-spacing: 1px;
  font-weight: 300;
  padding: 0 28px;
  font-size: 14px;
  display: block;
  color: #000;
  width: 100%;
}

.navbar-link:hover {
  text-decoration: none;
  color: #ffde03;
}

.navbar-link-focus {
  text-decoration: underline;
  color: #ff0266;
}

.navbar-mobile {
  display: none;
}

.navbar-desktop {
  display: block;
}

@media (max-width: 600px) {
  .navbar-layout, .navbar-list {
    flex-direction: column;
  }
  .navbar-head {
    padding-right: 15px;
    width: 100%;
  }
  .navbar-show .navbar-head {
    border-bottom-color: #eeeeee;
  }
  .navbar-show .navbar-list {
    max-height: 100vh !important;
  }
  .navbar-show .navbar-list li {
    opacity: 1;
  }
  .navbar-show .navbar-list li:nth-of-type(1) {
    transition-delay: 0.3s;
  }
  .navbar-show .navbar-list li:nth-of-type(2) {
    transition-delay: 0.6s;
  }
  .navbar-show .navbar-list li:nth-of-type(3) {
    transition-delay: 0.9s;
  }
  .navbar-list {
    background: #f4f4f4;
    max-height: 0;
  }
  .navbar-list li {
    transition: opacity .35s ease-in-out;
    display: block;
    width: 100%;
    opacity: 0;
  }
  .navbar-link {
    line-height: 48px;
    padding: 0 24px;
  }
  .navbar-menu {
    cursor: pointer;
  }
  .navbar-desktop {
    display: none;
  }
  .navbar-mobile {
    display: block;
  }
}

@media (min-width: 600px) {
  .navbar-layout {
    padding: 0 25px;
    display: flex;
  }
  .navbar-list {
    height: 60px;
  }
}

/* -- Styles (only affect text) -- */
.style-title, .style-title-font, .style-family-title, .style-font-title, .style-font-title-font, .style-font-family-title {
  font-family: Bungee Inline, sans-serif;
}

.style-light, .style-light-font, .style-family-light, .style-font-light, .style-font-light-font, .style-font-family-light {
  font-family: Varela Round, sans-serif;
}

.style-icon, .style-icon-font, .style-family-icon, .style-font-icon, .style-font-icon-font, .style-font-family-icon {
  font-family: Material Icons, sans-serif;
}

.style-regular, .style-regular-font, .style-family-regular, .style-font-regular, .style-font-regular-font, .style-font-family-regular {
  font-family: Roboto, sans-serif;
}

.style-bold, .style-bold-font, .style-family-bold, .style-font-bold, .style-font-bold-font, .style-font-family-bold {
  font-family: Montserrat, sans-serif;
}

.style, .style-sz, .style-size, .style-font, .style-font-sz, .style-font-size {
  font-size: 14px;
}

.style-8, .style-sz-8, .style-size-8, .style-font-8, .style-font-sz-8, .style-font-size-8 {
  font-size: 8px;
