.layer {
  font-family: "Roboto", sans-serif;
  position: static;
  line-height: 1.85;
  font-size: 18px;
  display: block;
  width: 100%;
  z-index: 0;
}

.layer-content {
  position: relative;
  z-index: 25;
}

.layer-image {
  position: absolute;
  overflow: hidden;
  display: block;
  height: 100%;
  width: 100%;
  z-index: 0;
  left: 0;
  top: 0;
}

.layer-image-overlay {
  position: absolute;
  display: block;
  opacity: 0.35;
  height: 100%;
  width: 100%;
  z-index: 5;
  left: 0;
  top: 0;
}

.layer-image-blur {
  filter: blur(3px);
}

.layer-shadow {
  box-shadow: 0 40px 85px rgba(0, 0, 40, 0.3);
}

.layer-shadow--1 {
  box-shadow: 0 20px 65px rgba(0, 0, 40, 0.12);
}

.layer-shadow--2 {
  box-shadow: 0 10px 85px rgba(0, 0, 40, 0.35);
}

